import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack} from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider,   RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
HabukhanAirtimeDiscount.propTypes = {
    discount: PropTypes.object,
  };
export default function HabukhanAirtimeDiscount({ discount}) {
    const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
 mtn_vtu_smart: Yup.string().required(),
mtn_vtu_api: Yup.string().required(),
mtn_vtu_awuf: Yup.string().required(),
mtn_vtu_agent: Yup.string().required(),
mtn_vtu_special: Yup.string().required(),
//    airtel vtu
airtel_vtu_smart: Yup.string().required(),
airtel_vtu_api: Yup.string().required(),
airtel_vtu_awuf: Yup.string().required(),
airtel_vtu_agent: Yup.string().required(),
airtel_vtu_special: Yup.string().required(),

// glo vtu
glo_vtu_smart: Yup.string().required(),
glo_vtu_api: Yup.string().required(),
glo_vtu_awuf: Yup.string().required(),
glo_vtu_agent: Yup.string().required(),
glo_vtu_special: Yup.string().required(),

// 9mobile vtu
mobile_vtu_smart: Yup.string().required(),
mobile_vtu_api: Yup.string().required(),
mobile_vtu_awuf: Yup.string().required(),
mobile_vtu_agent: Yup.string().required(),
mobile_vtu_special: Yup.string().required(),

// mtn share and sell
mtn_share_smart: Yup.string().required(),
mtn_share_api: Yup.string().required(),
mtn_share_awuf: Yup.string().required(),
mtn_share_agent: Yup.string().required(),
mtn_share_special: Yup.string().required(),
//    airtel share and sell
airtel_share_smart: Yup.string().required(),
airtel_share_api: Yup.string().required(),
airtel_share_awuf: Yup.string().required(),
airtel_share_agent: Yup.string().required(),
airtel_share_special: Yup.string().required(),

// glo share and sell
glo_share_smart: Yup.string().required(),
glo_share_api: Yup.string().required(),
glo_share_awuf: Yup.string().required(),
glo_share_agent: Yup.string().required(),
glo_share_special: Yup.string().required(),

// 9mobile  share and sell
mobile_share_smart: Yup.string().required(),
mobile_share_api: Yup.string().required(),
mobile_share_awuf: Yup.string().required(),
mobile_share_agent: Yup.string().required(),
mobile_share_special: Yup.string().required(),

// max and min airtime purchase
max_airtime: Yup.string().required(),
min_airtime: Yup.string().required()
  });

  const defaultValues = useMemo(
    () => ({
      mtn_vtu_smart: discount?.mtn_vtu_smart,
      mtn_vtu_agent: discount?.mtn_vtu_agent,
      mtn_vtu_awuf: discount?.mtn_vtu_awuf,
      mtn_vtu_api: discount?.mtn_vtu_api,
      mtn_vtu_special: discount?.mtn_vtu_special,
        //  airtel vtu
      airtel_vtu_smart: discount?.airtel_vtu_smart,
      airtel_vtu_agent: discount?.airtel_vtu_agent ,
      airtel_vtu_awuf: discount?.airtel_vtu_awuf,
      airtel_vtu_api: discount?.airtel_vtu_api,
      airtel_vtu_special: discount?.airtel_vtu_special,
    //    glo vtu
      glo_vtu_smart: discount?.glo_vtu_smart,
      glo_vtu_awuf: discount?.glo_vtu_awuf,
      glo_vtu_agent: discount?.glo_vtu_agent,
      glo_vtu_special: discount?.glo_vtu_special,
      glo_vtu_api: discount?.glo_vtu_api,

    //   9mobile vtu
    mobile_vtu_smart: discount?.mobile_vtu_smart,
    mobile_vtu_awuf: discount?.mobile_vtu_awuf,
    mobile_vtu_agent: discount?.mobile_vtu_agent,
    mobile_vtu_special: discount?.mobile_vtu_special,
    mobile_vtu_api: discount?.mobile_vtu_api,

    // mtn share and sell
    mtn_share_smart: discount?.mtn_share_smart,
    mtn_share_agent: discount?.mtn_share_agent,
    mtn_share_awuf: discount?.mtn_share_awuf,
    mtn_share_api: discount?.mtn_share_api,
    mtn_share_special: discount?.mtn_share_special,
      //  airtel share and sell
    airtel_share_smart: discount?.airtel_share_smart,
    airtel_share_agent: discount?.airtel_share_agent,
    airtel_share_awuf: discount?.airtel_share_awuf,
    airtel_share_api: discount?.airtel_share_api,
    airtel_share_special: discount?.airtel_share_special,
  //    glo share and sell
    glo_share_smart: discount?.glo_share_smart,
    glo_share_awuf: discount?.glo_share_awuf,
    glo_share_agent: discount?.glo_share_agent,
    glo_share_special: discount?.glo_share_special,
    glo_share_api: discount?.glo_share_api,

  //   9mobile share and sell
  mobile_share_smart: discount?.mobile_share_smart,
  mobile_share_awuf: discount?.mobile_share_awuf,
  mobile_share_agent: discount?.mobile_share_agent,
  mobile_share_special: discount?.mobile_share_special,
  mobile_share_api: discount?.mobile_share_api,
  // max and min airtime purchase
  max_airtime: discount?.max_airtime,
  min_airtime: discount?.min_airtime
    }),
    [discount]
  );
  const AccessToken =  window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount!== undefined) {
      reset(defaultValues);
    }
   
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]); 
  const {
    reset,
    setError,
    handleSubmit,
    formState: {errors, isSubmitting },
  } = methods;
 
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/edit/airtime/discount/account/${AccessToken}/system/secure`,data)
     swal('success', `Airtime Discount Updated Successfully`, 'success');
    } catch (error) {
        if (isMountedRef.current){
            setError('afterSubmit', error);
          }
    }
  };
   
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
     
        <Grid item xs={12} md={18}>
        {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
     
            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(3, 1fr)' },
              }}
            >              
                 <RHFTextField name="mtn_vtu_smart" label="MTN VTU SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_vtu_awuf" label="MTN VTU AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_vtu_agent" label="MTN VTU AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_vtu_api" label="MTN VTU API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_vtu_special" label="MTN VTU SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>

                 {/* airtel vtu */}
                 <RHFTextField name="airtel_vtu_smart" label="AIRTEL VTU SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_vtu_awuf" label="AIRTEL VTU AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_vtu_agent" label="AIRTEL VTU AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_vtu_api" label="AIRTEL VTU API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_vtu_special" label="AIRTEL VTU SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
           
                   {/* glo vtu */}

                   <RHFTextField name="glo_vtu_smart" label="GLO VTU SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_vtu_awuf" label="GLO VTU AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_vtu_agent" label="GLO VTU AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_vtu_api" label="GLO VTU API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_vtu_special" label="GLO VTU SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                      {/* 9mobile vtu */}
                 <RHFTextField name="mobile_vtu_smart" label="9MOBILE VTU SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_vtu_awuf" label="9MOBILE VTU AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_vtu_agent" label="9MOBILE VTU AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_vtu_api" label="9MOBILE VTU API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_vtu_special" label="9MOBILE VTU SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>

                 {/* mtn share and sell */}
                 <RHFTextField name="mtn_share_smart" label="MTN SHARE AND SELL SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_share_awuf" label="MTN SHARE AND SELL AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_share_agent" label="MTN SHARE AND SELL AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_share_api" label="MTN SHARE AND SELL API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mtn_share_special" label="MTN SHARE AND SELL SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>

                 {/* airtel share and sell */}
                 <RHFTextField name="airtel_share_smart" label="AIRTEL SHARE AND SELL SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_share_awuf" label="AIRTEL SHARE AND SELL AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_share_agent" label="AIRTEL SHARE AND SELL AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_share_api" label="AIRTEL SHARE AND SELL API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="airtel_share_special" label="AIRTEL SHARE AND SELL SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
           
                   {/* glo vtu */}

                   <RHFTextField name="glo_share_smart" label="GLO SHARE AND SELL SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_share_awuf" label="GLO SHARE AND SELL AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_share_agent" label="GLO SHARE AND SELL AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_share_api" label="GLO SHARE AND SELL API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="glo_share_special" label="GLO SHARE AND SELL SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                      {/* 9mobile vtu */}
                 <RHFTextField name="mobile_share_smart" label="9MOBILE SHARE AND SELL SMART" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_share_awuf" label="9MOBILE SHARE AND SELL AWUF" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_share_agent" label="9MOBILE SHARE AND SELL AGENT" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_share_api" label="9MOBILE SHARE AND SELL API" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="mobile_share_special" label="9MOBILE SHARE AND SELL SPECIAL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
               
            {/* max and min airtime purchase */}

            <RHFTextField name="min_airtime" label="MINIMUM AIRTIME" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFTextField name="max_airtime" label="MAXIMUM AIRTIME" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
             </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
               Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
